<template>
<li class="catalog__item">
            <router-link class="catalog__pic" :to= "{name: 'product', params: {id: product.id}}">
              <img :src="product.image" :alt="product.title">
            </router-link>

            <h3 class="catalog__title">
              <a href="#">
                {{product.title}}
              </a>
            </h3>

            <span class="catalog__price">
              {{ product.price | numberFormat }} ₽
            </span>
           <ul class="colors colors--black">
              <li class="colors__item" :value= "color.id" v-for= "color in product.colors" :key= "color.id">
                <label class="colors__label">
                  <input class="colors__radio sr-only" type="radio" name="color" :value= "color.id" v-model= "color.id">
                  <span class="colors__value" :style="`background-color: ${color.code}`">
                  </span>
                </label>
              </li>
            </ul>
</li>
</template>
<script>
import { API_BASE_URL } from '../config';
import axios from 'axios';
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";
export default {
  data() {
  return {
    currentColorId: null,
    colorsData: null,
  };
  },
  filters: {
    numberFormat
  },
  methods: {
    gotoPage
  },
  props: ['product', 'colorId'],
  computed: {
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    }
  },
  watch: {
    color(value) {
      this.currentColorId = value;
    }
  },
  methods: {
    submit() {
      this.$emit('update:colorId', this.currentColorId);
    },
    reset() {
      this.$emit('update:colorId', null);
    },
    loadColors() {
      axios.get(API_BASE_URL + '/api/colors')
      .then(response => this.colorsData = response.data);
    }
  },
  created() {
    this.loadColors();
  }
}
</script>

