import BasaFormField from "@/components/BasaFormField";
export default {
  props: ['title', 'error', 'placeholder', 'value'],
  components: {BasaFormField},
  computed: {
    dataValue: {
      get() {
       return this.value;
      },
      set(value){
        this.$emit('input', value);
      }
    }
  }
}
